"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import CompareMetricsGrid from "@/app/components/CompareMetricsGrid";
import CompareInsights from "@/app/components/CompareInsights";
import LoadingScreen from "@/app/components/LoadingScreen";

export default function CompareResultPage() {
  const { tickers } = useParams() as { tickers: string };
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!tickers) return;

    setLoading(true);

    fetch("http://localhost:8000/compare-summary", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ tickers: tickers.split(",") }),
    })
      .then((res) => res.json())
      .then((json) => {
        setData(json);
        setLoading(false);
      })
      .catch((err) => {
        console.error("compare-summary error:", err);
        setLoading(false);
      });
  }, [tickers]);

  if (loading) return <LoadingScreen />;

  if (!data || data.error) {
    return (
      <div className="text-white p-6">
        <h2 className="text-red-500 font-bold text-xl">Failed to load comparison.</h2>
        <p>{data?.error || "Unknown error"}</p>
      </div>
    );
  }

  return (
    <main className="p-6 text-white">
      <h1 className="text-2xl font-bold mb-4">
        Comparing {tickers.replaceAll(",", " vs ")}
      </h1>
      <CompareMetricsGrid data={data.tickers} />
      <CompareInsights insight={data.insight} />
    </main>
  );
}
