type CompanyMetric = {
    ticker: string;
    market_cap: number;
    pe_ratio: number;
    roe: number;
    profit_margin: number;
  };
  
  export default function CompareMetricsGrid({ data }: { data: CompanyMetric[] }) {
    return (
      <div className="overflow-x-auto mt-6">
        <table className="table-auto w-full text-left text-sm text-white">
          <thead>
            <tr className="bg-zinc-900">
              <th className="p-2">Ticker</th>
              <th className="p-2">Market Cap</th>
              <th className="p-2">P/E</th>
              <th className="p-2">ROE</th>
              <th className="p-2">Profit Margin</th>
            </tr>
          </thead>
          <tbody>
            {data.map((c) => (
              <tr key={c.ticker} className="hover:bg-zinc-800">
                <td className="p-2 font-bold">{c.ticker}</td>
                <td className="p-2">${(c.market_cap / 1e9).toFixed(1)}B</td>
                <td className="p-2">{c.pe_ratio?.toFixed(2) ?? "–"}</td>
                <td className="p-2">{(c.roe * 100).toFixed(1)}%</td>
                <td className="p-2">{(c.profit_margin * 100).toFixed(1)}%</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
  